@import "variables/variables.scss";
@import "mixins/mixins.scss";

.page-footer {
  min-width: 320px;
  min-height: 236px;
  
  text-align: center;

  background-color: $colorAddBg;

  &__wrapper {
    padding: 0 20px;
    height: 100%;
  }

  &__logo {
    margin-top: 39px;
    margin-bottom: 31px;
  }

  &__social {
    padding-top: 21px;
    padding-bottom: 11px;

    border-top: 1px solid;
    border-bottom: 1px solid;
    border-color: $colorBorderProgramm;
  }

  @media (min-width: $tabletWidth) {
    min-height: 150px;

    text-align: left;

    &__wrapper {
      display: flex;
      justify-content: space-between;
  
      margin: auto 0;
      padding: 0 30px;

      min-height: 150px;
    }

    &__logo,
    &__social,
    &__copyright {
      margin: auto 0;
    }

    &__social {
      padding: 0;
  
      border: none;
    }
  }

  @media (min-width: $desktopWidth) {
    &__wrapper {
      padding: 0 110px;
    }
  }
}
